<template>
  <div>
    <!--  -->
    <Breadcrumb name1="" name2="" />

    <!--  -->
    <el-card>
      <el-row>
        <el-col :span="8">
          <el-input
            placeholder=""
            class="input-with-select"
            v-model="queryInfo.query"
            clearable
            @clear="getOrderList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getOrderList"
            ></el-button>
          </el-input>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="orderList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="order_number" label=""></el-table-column>
        <el-table-column prop="order_price" label=""></el-table-column>
        <el-table-column prop="order_pay" label="">
          <template slot-scope="scope">
            <el-tag type="danger" v-if="scope.row.order_pay === '0'"
              ></el-tag
            >
            <el-tag type="success" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label=""></el-table-column>
        <el-table-column prop="create_time" label="">
          <template slot-scope="scope">
            {{ scope.row.create_time | date }}
          </template>
        </el-table-column>
        <el-table-column label="" width="200px">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="showAddressDialog"
            ></el-button>
            <el-button
              type="success"
              icon="el-icon-location"
              size="mini"
              @click="showProgress"
            ></el-button>
            <el-button
              type="warning"
              icon="el-icon-search"
              size="mini"
              @click="goOrderPage(scope.row.order_id)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 20, 50]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addressVisible"
      width="50%"
      @close="addressDialogClose"
    >
      <el-form
        :model="addressForm"
        :rules="addressRules"
        ref="addressFormRef"
        label-width="100px"
      >
        <el-form-item label="/" prop="city">
          <el-cascader
            :options="cityOptions"
            :props="cityProps"
            v-model="addressForm.city"
            clearable
          >
          </el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address">
          <el-input v-model="addressForm.address"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addressVisible = false"></el-button>
        <el-button type="primary" @click="editAddress"></el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="progressVisible" width="50%">
      <el-timeline>
        <el-timeline-item
          v-for="(item, index) in progressInfo"
          :key="index"
          :timestamp="item.time"
        >
          {{ item.context }}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import Breadcrumb from '../Breadcrumb.vue'
import citydata from './citydata'
export default {
  name: 'OrderList',
  components: { Breadcrumb },
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      orderList: [],
      total: 0,
      addressVisible: false,
      addressForm: {
        city: '',
        address: ''
      },
      addressRules: {
        city: [{ required: true, message: '/', trigger: 'blur' }],
        address: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      cityOptions: citydata,
      cityProps: {
        label: 'label',
        value: 'value',
        children: 'children',
        expandTrigger: 'hover'
      },
      progressVisible: false,
      progressInfo: []
    }
  },
  methods: {
    // 
    async getOrderList() {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      } else {
        this.orderList = res.data.goods
        this.total = res.data.total
      }
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrderList()
    },
    // 
    showAddressDialog() {
      this.addressVisible = true
    },
    // 
    addressDialogClose() {
      this.$refs.addressFormRef.resetFields()
    },
    // 
    editAddress() {
      this.$refs.addressFormRef.validate((valid) => {
        if (!valid) return
        this.addressVisible = false
      })
    },
    // 
    async showProgress() {
      const { data: res } = await this.$http.get('/kuaidi/1106975712662')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      } else {
        this.progressInfo = res.data
        this.progressVisible = true
      }
    },
    // 
    goOrderPage(id) {
      this.$router.push(`order?id=${id}`)
    }
  },
  created() {
    this.getOrderList()
  }
}
</script>
