<template>
  <div>
    <!--  -->
    <Breadcrumb name1="" name2="" />

    <!--  -->
    <el-card>
      <el-form :model="order" label-width="100px">
        <el-form-item label="">
          <el-input v-model="order.user_id" disabled></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="order.order_number" disabled></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="order.order_price" disabled></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="order.order_pay">
            <el-radio label="0" v-if="order.order_pay === '0'"></el-radio>
            <el-radio label="1"></el-radio>
            <el-radio label="2"></el-radio>
            <el-radio label="3"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="order.is_send" disabled>
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="order.create_time" disabled> </el-input>
        </el-form-item>
        <el-button type="primary" @click="handleChange"></el-button>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import Breadcrumb from '../Breadcrumb.vue'
export default {
  name: 'OrderPage',
  components: { Breadcrumb },
  data() {
    return {
      id: '',
      order: {}
    }
  },
  methods: {
    async getOrder() {
      const { data: res } = await this.$http.get(`orders/${this.id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      } else {
        this.order = res.data
      }
    },
    async handleChange() {
      const status = {
        order_pay: this.order.order_pay,
        order_price: this.order.order_price
      }

      const { data: res } = await this.$http.put(`orders/${this.id}`, status)
      if (res.meta.status !== 201) {
        return this.$message.error('')
      } else {
        this.$router.push('/orders')
        this.$message.success('')
      }
    }
  },
  created() {
    this.id = this.$route.query.id
    this.getOrder()
  }
}
</script>
